/* 최상단 띠배너 */
header{height: 190px; box-shadow: 0 2px 4px #9b9b9b;}
.h_banner{
  background: #5f0080;
  color: #fff; margin: 0;
  padding: 14px 0; font-size: 14px;
  text-align: center;
}
.h_banner span:first-child{font-weight: bold;}
.h_banner span:last-child{float: right; cursor: pointer;}

/* 헤더 랩 */
.header_wrap{width: 1050px; margin: 0 auto;}

/* 오른쪽 회원가입 등등 */
.jolocu{display: flex; justify-content: right;}
.jolocu li{width:auto; font-size: 12px; padding: 10px 2px;}
.jolocu li::after{
  display: inline-block;
  content: "";
  width: 1px; height: 100%;
  background-color: #ccc;
  margin: 0 8px; transform: translateY(2px);
}
.jolocu li:last-child::after{display: none;}
.jolocu li:last-child span{display:inline-block; transform: translateY(1px);}

/* 헤더 중간 */
.h_middle{
  display: flex;
}
.h_middle h1{display: block; width: 82px;}
.h_middle .mode{
  display: flex;
  font-size: 18px; color: #b5b5b5;
  padding-left: 5px;
  align-items: center;
}
.h_middle .mode li{ padding: 0 8px; font-weight: bold;}
.h_middle .mode li:hover{color: #5f0080;}
.c_pur{color: #5f0080;}
.h_middle .mode li::after{
  display: inline-block;
  content: "";
  width: 1px; height: 16px;
  background: #ccc;
  transform: translateX(8px);
}
.h_middle .mode li:last-child::after{display: none;}
.h_middle .search{
  width: 400px; height: 48px; margin: 0 auto !important;
  border: 1px solid #5f0080;
  border-radius: 8px;
}
.h_middle .search input{
  outline: none;
  border: none;
  background: transparent;
  text-indent: 20px;
  font-size: 16px;
  width: 300px; height: 100%;
  float: left;
}
.h_middle .search span{
  width: 30px;
  float: right;
  font-size: 30px; margin: 10px;
  color: #5f0080;
}
.h_middle .h_etc{display: flex;}
.h_middle .h_etc li{
  width: 36px;
  padding: 12px 16px;
  font-size: 30px;
}
.h_middle .h_etc li:hover{color: #5f0080;}

/* 내비게이션 */
.h_bottom{display: flex; align-items: center; justify-content: space-between; margin-top: 18px;}
.h_bottom .gnb{
  font-size:16px; 
  font-weight: bold; color: #333;
  transform: translateY(-8px);
}
.h_bottom .gnb span{
  display: inline-block;
  font-size: 30px; 
  transform: translateY(10px);
}
.h_bottom ul{display: flex; justify-content: space-around;}
.h_bottom ul li{
  font-size: 16px; font-weight: bold;
  padding: 0 50px;
}
.h_bottom ul li:hover{
  color: #5f0080;
  text-decoration: underline;
}
.del{
  border: 1px solid #ededed;
  font-size: 14px;
  color: 666;
  padding: 6px 16px;
  border-radius: 50px;
  background: transparent;
  cursor: pointer;
}
.del span{color: #5f0080; font-weight: bold;}