article.BestItem{padding: 40px 0; width: 1050px; margin: 0 auto;}
.BestItem h2{
  text-align: center;
  font-size: 28px; font-weight: bold;
}
.BestItem p{font-size: 16px; color: #999; text-align: center; margin: 20px 0 27px;}
.BestItem ul{display: flex; justify-content: space-between;}
.BestItem ul li{width: 250px; position: relative;}
.BestItem ul li img{width: 100%;}
.BestItem ul li p{
  text-align: left; font-size: 16px; color: #222; margin: 0;
  width: 250px; overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.BestItem ul li p:nth-of-type(1){
  position: absolute; top: 0;
}
.BestItem ul li p:nth-of-type(1) span{
  display: inline-block;
  background: #bd76ff;
  padding: 10px; margin: 6px;
  border-radius: 5px;
  font-size: 14px; font-weight: bold; color: #fff;
}
.BestItem ul li p:nth-of-type(2) button{width: 100%; height: 36px; font-size: 16px; color: #333; background:#fff; border: 1px solid #dedede; border-radius: 2px; margin-top: 6px; cursor: pointer;}
.BestItem ul li p:nth-of-type(2) button span{padding: 0 4px;}
.BestItem ul li p:nth-of-type(3){margin: 8px 0; line-height: 24px;}
.BestItem ul li p:nth-of-type(4){font-size: 14px; color: #b5b5b5; text-decoration: line-through;}
.BestItem ul li p:nth-of-type(5){font-weight: bold; margin-top: 7px;}
.BestItem ul li p:nth-of-type(5) span{color: #fa622f; margin-right: 7px;}
